import React, { useState } from 'react';
import { sanitizeInput } from '../utils/sanitizeInput'; // Importamos la funcin de utilidad

const SolveCryptogramForm = ({ userId, onNewInteraction }) => {
  const [cryptogram, setCryptogram] = useState('');
  const [clues, setClues] = useState('');
  const [solution, setSolution] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setSolution(null);
    
    const { sanitizedCryptogram, sanitizedClues } = sanitizeInput(cryptogram, clues);

    try {
      const response = await fetch('/api/solve', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          cryptogram: sanitizedCryptogram,
          clues: sanitizedClues,
          user_id: userId
        }),
      });
      const data = await response.json();
      if (!response.ok) throw new Error(data.error || 'Error al resolver el criptograma');
      setSolution(data.solution);
      onNewInteraction();
    } catch (error) {
      // Manejo de errores
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <h2>Resolver Criptograma</h2>
      <textarea
        value={cryptogram}
        onChange={(e) => setCryptogram(e.target.value)}
        required
      />
      <input
        type="text"
        value={clues}
        onChange={(e) => setClues(e.target.value)}
      />
      <button type="submit" disabled={isLoading}>
        {isLoading ? 'Resolviendo...' : 'Resolver'}
      </button>
      {solution && <div>Solucin: {solution}</div>}
    </form>
  );
};

export default SolveCryptogramForm;